<template>
    <div class="main">
        <div v-wechat-title="$route.meta.title"></div>
        <!-- 活动报名 -->
        <div>
            <div class="signInfo">
                <div class="infoWrap" v-if="!$route.query.isOfficialAccount">
                    <div class="wrap">
                        <span><p>*</p>邀请码：</span>
                        <input type="text" placeholder="请填写" v-model="inviteCode"/>
                    </div>

                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span><p>*</p>学生名称：</span>
                        <input type="text" placeholder="请填写" v-model="studentName"/>
                    </div>

                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span><p>*</p>手机号码：</span>
                        <input @change="userPhoneTest(studentPhone)" type="number" placeholder="请填写"
                               v-model="studentPhone"/>
                    </div>

                    <p class="yzm" v-if="isVerify" @click="yzm(studentPhone)">{{yzmContent}}</p>
                    <p class="yzm" v-if="!isVerify">{{yzmContent}}</p>
                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span><p>*</p>验证码：</span>
                        <input type="text" placeholder="请填写" v-model="verifyCode"/>
                    </div>

                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span><p>*</p>所在学校：</span>
                        <input type="text" placeholder="请填写" v-model="schoolName" @input="getSearchSchool()"/>
                    </div>
                    <div class="ulWrap" v-if="searchSchoolList&&isShow">
                        <ul>
                            <li v-for="(item,index) in searchSchoolList"
                                @click="clickSchool(item.schoolName,item.schoolId)">
                                {{item.schoolName}}
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="infoWrap">
                    <div class="wrap" @click="pickerStudentArtsScience()">
                        <span><p>*</p>学生文理：</span>
                        <!--<input type="text" placeholder="请填写" v-model="studentArtsScience" @change="pickerStudentArtsScience()"/>-->
                        <span v-if="!studentArtsScience">请选择</span>
                        <span v-if="studentArtsScience">{{studentArtsScience}}</span>
                    </div>

                    <!-- <text class="yzm">获取验证码</text>-->
                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span style="display: flex">分数(选填)：</span>
                        <input type="number" placeholder="请填写" v-model="studentTotalScore"/>
                    </div>

                    <!-- <text class="yzm">获取验证码</text>-->
                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span style="display: flex">排名(选填)：</span>
                        <input type="number" placeholder="请填写" v-model="studentRank"/>
                    </div>

                    <!-- <text class="yzm">获取验证码</text>-->
                </div>
                <div class="infoWrap">
                    <div class="wrap">
                        <span><p>*</p>家长名称：</span>
                        <input type="text" placeholder="请填写" v-model="parentName"/>
                    </div>

                </div>
                <div class="infoWrap">
                    <div class="wrap"><span><p>*</p>家长手机：</span>
                        <input @change="parentPhoneTest(parentPhone)" type="number" placeholder="请填写"
                               v-model="parentPhone"/>
                    </div>
                </div>
            </div>
            <div class="reg">
                <div v-if="$route.query.isOfficialAccount||$route.query.agentType==1||$route.query.agentType==2"
                     @click="goRegAgent()">我想成为代理
                </div>
                <img v-if="$route.query.isOfficialAccount||$route.query.agentType==1||$route.query.agentType==2"
                     @click="goRegAgent()" src="../img/agent.png" alt="">
            </div>
            <div class="submit">
                <button v-if="$route.query.isOfficialAccount" @click="submit()">提交</button>
                <button v-if="!$route.query.isOfficialAccount" @click="submit1()">提交</button>
            </div>
            <div style="display: flex;justify-content: space-between;align-items: center;padding:0 10px">
                <div class="service" @click="goServiceProcess()">服务流程</div>
                <div class="service" @click="goService()">服务详情</div>
                <div class="service" @click="goGuanZhu()">关注公众号</div>
            </div>
            <div :class="{mask:true, hide:isMask}" @click="close()"></div>
            <div ref="cart-panel" style="overflow-y: scroll;height: 70%;"
                 :class="['cart-panel', movePanel,'buy_box']" v-if="productList.length>0">
                <div class="title">
                    <img @click="close()" src="../img/cha.png" alt="">
                    <span>选择服务套餐</span>
                </div>
                <div class='price'><span style='color:red;'>￥{{price}}</span></div>
                <div class="type" style="font-size: 14px;color: #999;margin-bottom: 15px;">套餐类型</div>
                <span :class="'product'+(index==i ?' active':'')" @click="click(index)"
                      v-for="(item,index) in productList"
                      :key="index">{{item.productName}}</span>
                <!--<button type="primary" bindtap="primary" bindtap='hideBuyModal'> 确定 </button>-->
                <div class="content" style="height: 50%;overflow-y: scroll">
                    <div ref="swiper-container" class="swiper-container swiper-main">
                        <div class="swiper-wrapper">
                            <div class="swiper-slide" v-for="(item,index) in productImgs" :key="index">
                                <img v-if="productImgs.length>0" :src="item.imgUrl" alt="">
                                <img v-if="productImgs.length==0" src="../img/icon_empty_1.png" alt="">
                            </div>
                        </div>
                        <div ref="swiper-pagination" class="swiper-pagination"></div>
                    </div>
                    <div class="titlea">
                        <div>
                            <div class="productTitle">{{goodsName}}</div>
                            <div class="gameComponent">分类名称：{{categoryName}}</div>
                        </div>
                        <!--<div class="price-wrap">
                            <div>价格</div>
                            <div class="price">￥599.00</div>
                        </div>-->
                    </div>
                    <div class="developTarget">
                        <div class="developTitle">
                            <div class="kuai"></div>
                            <div class="title1">产品介绍</div>
                        </div>
                        <div class="developContent">
                            {{goodsDescirbe}}
                        </div>
                    </div>
                    <div class="gameDetail">
                        <div class="gameTitle">
                            <div class="kuai"></div>
                            <div class="title1">产品详情</div>
                        </div>
                        <div class="gameContent" v-html="goodsDetails">

                        </div>
                    </div>
                </div>
                <!--<div style="color: #999;text-align: right" @click="goDetail('/productDetail?id='+productId)">查看服务详情
                </div>-->
                <div class="btn" v-if="!isOrder" @click="pay()">支付</div>
                <div class="btn" v-if="isOrder">支付</div>
            </div>
            <div class="chooseProduct" v-if="arr.length>0">
                <img src="../img/signup.png" alt="">
                <div>报名信息</div>
            </div>
            <div class="content" v-if="arr.length>0">
                <div class="studentWrap">
                    <div class="studentName"><span>学生姓名：{{info.studentName}}</span><span
                            style="width: 40%;">电话：{{info.studentPhone}}</span>
                    </div>
                    <div class="studentInfo">
                        <div class="left">
                            <div class="phone">学校名称：{{info.studentSchoolName}}</div>
                            <div class="school">报名时间：{{info.updateTime?info.updateTime:info.createTime}}
                            </div>
                        </div>
                        <div class="center">
                            <div class="price1" v-if="info.studentArtsScience==0&&info.studentTotalScore">
                                总分：{{info.studentTotalScore}}分 文科
                            </div>
                            <div class="price1"
                                 v-else-if="info.studentArtsScience==1&&info.studentTotalScore">
                                总分：{{info.studentTotalScore}}分 理科
                            </div>
                            <div class="price1" v-else>总分：未知 {{info.studentArtsScience==0?'文科':'理科'}}</div>
                            <div class="price2" v-if="info.studentRank">排名：第{{info.studentRank}}名</div>
                            <div class="price2" v-else="info.studentRank">排名：未知</div>
                        </div>

                    </div>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
    import request from '../../../../static/js/request';
    import result from '../../../../static/js/result';
    import getParam from '../../../../static/js/getParam';
    import wxInfo from '../../../../static/js/wxPublicInfo'
    import Swiper from '../../../../static/js/swiper/swiper';

    export default {
        data() {
            return {
                agentPid: '',//父级代理ID
                inviteCode: '', //邀请码
                activityId: 0, //活动ID
                studentTotalScore: '',//学生总分
                studentRank: '',  //学生排名
                studentArtsScience: '', //学生文理0为文科1为理科2为未知
                studentArtsScienceId: 0,
                studentId: 0,//学生ID
                sid: 0, //学生ID
                studentName: '',//学生姓名
                studentPhone: '',//学生手机号
                schoolName: '',//学校名
                parentName: '',//家长姓名
                parentPhone: '',//家长手机
                schoolList: [],//学校数组
                searchSchoolList: [],
                docmHeight: document.documentElement.clientHeight,  //默认屏幕高度
                showHeight: document.documentElement.clientHeight,   //实时屏幕高度
                topShow: false,
                isMask: true,//是否显示遮罩层
                movePanel: 'down',//产品支付是否弹出
                productList: [],//产品数组
                isClick: false,
                price: 0,//产品支付价格
                i: 0,
                productName: '',//产品名
                productId: 0,//产品ID
                productUnit: '',
                isShow: true,//学校提示数组框显示状态
                verifyCode: '',//验证码
                isVerify: true,//验证码状态
                yzmContent: '获取验证码',
                info: {},//报名信息
                arr: [],//报名数组
                token: '',//token值
                pwd: '',//用于获取token的密码
                /*-------------*/
                goodsName: "",//支付弹窗内产品名
                goodsDescirbe: "",//支付弹窗内产品描述
                goodsDetails: "",//支付弹窗内产品详情
                productImgs: [],//产品轮播图
                categoryName: "",//分类名
                isBoxMask: 'none',
                isOrder: false,//是否已下单
                isTip: false,//如已下单提示
                isSignUp: false
                /*---------*/
            }
        },
        mounted() {
            window.onresize = () => {
                return (() => {
                    this.showHeight = document.body.clientHeight;
                })()
            }
        },
        created() {
            /**
             * 重定向地址
             * **/
            let REDIRECT_URI = ''
            if (this.$route.query.isOfficialAccount) {
                /*公众号内进入重定向地址*/
                REDIRECT_URI = encodeURIComponent(this.$host + '/activities.html#/activitiesSignUp?activityId=' + this.$route.query.activityId + '&isOfficialAccount=' + this.$route.query.isOfficialAccount)
            } else {
                /*分享代理扫码进入重定向地址*/
                if (this.$route.query.agentType == 3) {
                    REDIRECT_URI = encodeURIComponent(this.$host + '/activities.html#/activitiesSignUp?agentId=' + this.$route.query.agentId + '&agentName=' + this.$route.query.agentName + '&agentType=' + this.$route.query.agentType + '&agentPid=' + this.$route.query.agentPid + '&productId=' + this.$route.query.productId + '&agentCode=' + this.$route.query.agentCode + '&agentPname=' + this.$route.query.agentPname)
                } else if (this.$route.query.agentType == 4) {
                    /*普通代理扫码进入重定向地址*/
                    REDIRECT_URI = encodeURIComponent(this.$host + '/activities.html#/activitiesSignUp?agentId=' + this.$route.query.agentId + '&agentName=' + this.$route.query.agentName + '&agentType=' + this.$route.query.agentType + '&productId=' + this.$route.query.productId + '&agentCode=' + this.$route.query.agentCode)
                } else {
                    if (this.$route.query.productId) {
                        /*省代和市代产品扫码进入重定向地址*/
                        REDIRECT_URI = encodeURIComponent(this.$host + '/activities.html#/activitiesSignUp?agentId=' + this.$route.query.agentId + '&agentName=' + this.$route.query.agentName + '&agentType=' + this.$route.query.agentType + '&agentCode=' + this.$route.query.agentCode + '&productId=' + this.$route.query.productId)
                    } else {
                        /*省代和市代活动扫码进入重定向地址*/
                        REDIRECT_URI = encodeURIComponent(this.$host + '/activities.html#/activitiesSignUp?activityId=' + this.$route.query.activityId + '&agentId=' + this.$route.query.agentId + '&agentName=' + this.$route.query.agentName + '&agentType=' + this.$route.query.agentType + '&agentCode=' + this.$route.query.agentCode)
                    }


                }
            }
            /*网页授权*/
            if (!getParam('code')) {
                console.log(REDIRECT_URI, wxInfo.wxAppId)
                location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${wxInfo.wxAppId}&redirect_uri=${REDIRECT_URI}&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect`
            } else {
                /*记录已报名的学生的信息，如已报名自动填写*/
                if (localStorage['studentInfo']) {
                    let studentInfo = JSON.parse(localStorage['studentInfo']);
                    this.isShow = false;
                    this.studentName = studentInfo.studentName;
                    this.studentPhone = studentInfo.studentPhone;
                    this.schoolId = studentInfo.schoolId ? studentInfo.schoolId : '';
                    this.schoolName = studentInfo.schoolName;
                    this.studentTotalScore = studentInfo.studentTotalScore;
                    this.studentRank = studentInfo.studentRank;
                    this.studentArtsScienceId = studentInfo.studentArtsScienceId;
                    this.studentArtsScience = studentInfo.studentArtsScience;
                    this.parentName = studentInfo.parentName;
                    this.parentPhone = studentInfo.parentPhone;
                }

            }
            this.isShow = false
            this.getStudentInfo()
            //this.getWxInfo();
            this.getDetail();
            this.getToken()
            this.getSchool();

            //this.getDetail1();
            if (this.$route.query.agentType == 3 || this.$route.query.agentType == 4) {
                this.getAgentProduct();
            } else if (this.$route.query.productId) {
                this.getAgentProduct();
            }
        },
        methods: {
            /**
             * 根据商品ID获取商品详情
             * **/
            async getDetail1() {
                if (this.productId) {
                    let params = {
                        productId: this.productId
                    };
                    let getDetailInfo = await request('/api/open/product/selectProductInfoById',
                        'post', params, 'application/x-www-form-urlencoded')
                    // loading.hide()
                    let res = result(getDetailInfo)
                    console.log(res)
                    // loading.hide()
                    if (!res) return;
                    console.log('返回结果', getDetailInfo)
                    this.goodsName = getDetailInfo.data.productName ? getDetailInfo.data.productName : '';
                    this.goodsDescirbe = getDetailInfo.data.productDescribe ? getDetailInfo.data.productDescribe : '';
                    this.goodsDetails = getDetailInfo.data.productDetails ? getDetailInfo.data.productDetails : '';
                    this.categoryName = getDetailInfo.data.productCategoryName ? getDetailInfo.data.productCategoryName : '';
                    this.productImgs = getDetailInfo.data.productImgs ? getDetailInfo.data.productImgs : [];
                    this.$nextTick(() => {
                        new Swiper(this.$refs['swiper-container'], {
                            pagination: {
                                el: this.$refs['swiper-pagination'],
                                clickable: true
                            },
                            autoplay: {
                                delay: 3000,
                                stopOnLastSlide: false,
                                disableOnInteraction: false,
                            },
                        })
                    })
                }

            },
            /**
             * 关注公众号按钮
             * **/
            goGuanZhu() {
                location.href = 'https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzI5ODE1NTAwNA==#wechat_redirect'
            },
            tab() {
                weui.tab('#tab', {
                    defaultIndex: 0,
                    onChange: function (index) {
                        console.log(index);
                    }
                });
            },

            goDetail(url) {
                this.$router.push(url);
            },
            /*注册代理*/
            goRegAgent() {
                if (this.$route.query.isOfficialAccount) {
                    this.$router.push('/regAgent?phone=' + this.studentPhone + '&name=' + this.studentName)
                } else {
                    this.$router.push('/regAgent?agentId=' + this.$route.query.agentId + '&phone=' + this.studentPhone + '&name=' + this.studentName)
                }

            },
            /**
             * 获取活动详情
             * **/
            async getDetail() {
                console.log('接收参数', this.$route.query.id)
                let params = {
                    activityId: this.$route.query.activityId
                };

                if (this.$route.query.activityId) {
                    let getDetailInfo = await request('/api/open/activity/selectActivityInfoById',
                        'post', params, 'application/x-www-form-urlencoded')
                    // loading.hide()
                    let res = result(getDetailInfo)
                    console.log(getDetailInfo)

                    this.theme = res.activityName;
                    this.activityAddress = res.activityAddress;
                    this.activityContent = res.activityDetails;
                    this.activityTime = res.activityTime;
                    this.productList = res.productInfos;
                    if (res.productInfos.length > 0) {
                        /*活动所关联的产品*/
                        this.price = Number(this.productList[0].productPrice).toFixed(2);
                        this.productName = this.productList[0].productName
                        this.productId = this.productList[0].productId
                        this.productUnit = this.productList[0].productUnit
                        for (let item of this.productList) {
                            if (item.productImgs) {
                                if (item.productImgs[0].imgUrl) {
                                    item.img = item.productImgs[0].imgUrl
                                } else {
                                    item.img = ''
                                }
                            } else {
                                item.img = ''
                            }

                        }
                        this.$nextTick(() => {
                            this.getDetail1()
                        })
                    }

                }


            },
            /*获取学生微信获取信息*/
            /*async getWxInfo() {
                console.log('code值', getParam('code'), this.$route.query.code)
                let params = {
                    code: this.$route.query.code
                }
                let getWxInfo = await request('/api/open/user/selectUserStudentWxId',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(getWxInfo)
                if (!res) return;
                console.log('获取信息', getWxInfo)
                if (getWxInfo.data) {
                    this.studentName = getWxInfo.data.studentName;
                    this.studentPhone = getWxInfo.data.studentPhone;
                    this.sid = getWxInfo.data.studentId;
                    this.studentRank = getWxInfo.data.studentRank ? getWxInfo.data.studentRank : '';
                    this.studentTotalScore = getWxInfo.data.studentTotalScore ? getWxInfo.data.studentTotalScore : '';
                    this.schoolName = getWxInfo.data.studentSchoolName;
                    this.schoolId = getWxInfo.data.studentSchoolId;
                    this.studentArtsScienceId = getWxInfo.data.studentArtsScience;
                }
            },*/
            /**
             * 获取二级代理和普通代理所关联的产品
             * **/
            async getAgentProduct() {
                let params = {}
                let productInfo = await request(
                    '/api/open/product/selectProductInfoByisRoyalty',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(productInfo)
                if (!res) return;
                console.log('产品', productInfo)
                this.productList.push(productInfo.data)
                this.productId = productInfo.data.productId
                this.price = Number(productInfo.data.productPrice).toFixed(2);
                this.productName = productInfo.data.productName
                this.productId = productInfo.data.productId
                this.productUnit = productInfo.data.productUnit
                this.$nextTick(() => {
                    this.getDetail1()
                })
            },
            /**
             * 根据报名学生的账号密码获取token
             * **/
            async getToken() {
                let params = {
                    account: this.studentPhone,
                    password: this.pwd,
                    loginType: 2
                }
                let loginInfo = await request('/api/open/token',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(loginInfo)
                if (!res) return;
                console.log('登陆成功信息', loginInfo)
                this.token = loginInfo.msg
                this.$nextTick(() => {
                    this.getActivityStaff()
                })
            },
            /**
             * 根据手机号获取学生信息
             * **/
            async getStudentInfo() {

                let params1 = {
                    //access_token: this.token,
                    //agentId:this.$route.query.agentId,
                    account: this.studentPhone
                }
                let info = await request(
                    '/api/open/user/selectUserStudentPhone',
                    'post', params1, 'application/x-www-form-urlencoded')
                // loading.hide()

                let res1 = result(info);
                if (!res1) return
                if (info.data) {
                    this.sid = info.data.studentId;
                    this.pwd = info.data.studentPassword
                }
                this.$nextTick(() => {
                    this.getToken()
                })
            },
            /**
             * 选择文理
             * **/
            pickerStudentArtsScience() {
                let _this = this;
                let array = [
                    {label: '文科', id: 0},
                    {label: '理科', id: 1},
                    {label: '未知', id: 2}
                ]
                weui.picker(array, {
                    className: 'custom-classname',
                    container: 'body',
                    defaultValue: [],
                    onChange: function (result) {
                        console.log(result)
                        _this.studentArtsScienceId = result[0].id;
                    },
                    onConfirm: function (result) {
                        console.log(result);
                        _this.studentArtsScience = result[0].label;
                        _this.studentArtsScienceId = result[0].id;
                    },
                    id: 'singleLinePicker'
                });
            },
            /**
             * 验证手机号
             * **/
            userPhoneTest(phone) {
                this.getStudentInfo()
                if (!(/^1[3456789]\d{9}$/.test(phone))) {
                    weui.alert("请输入正确手机号");
                    this.studentPhone = '';
                    return false;
                }
            },
            /**
             * 验证家长手机
             * **/
            parentPhoneTest(phone) {
                if (!(/^1[3456789]\d{9}$/.test(phone))) {
                    weui.alert("请输入正确手机号");
                    this.parentPhone = '';
                    return false;
                }
            },

            /**
             * 获取学校信息
             * */
            async getSchool() {
                let params = {}
                let getClassInfo = await request('/api/open/school/selectBaseSchoolList',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getClassInfo)
                // loading.hide()
                if (!info) return;
                this.schoolList = getClassInfo.data
                console.log('学校列表', this.schoolList)
            },
            /**
             * 搜索提示学校信息
             * */
            async getSearchSchool() {
                this.searchSchoolList = []
                this.isShow = true;
                console.log(this.schoolName)
                if (this.schoolName) {
                    for (let item of this.schoolList) {
                        if (item.schoolName.indexOf(this.schoolName) != -1) {
                            this.searchSchoolList.push(item)
                        }
                    }
                    console.log('学校列表', this.searchSchoolList)
                    return false;
                } else {
                    this.isShow = false
                }
                console.log('学校列表', this.searchSchoolList)
            },
            /**
             * 选学校
             * */
            pickerSchool() {
                let _this = this;
                if (_this.schoolList.length != 0) {
                    weui.picker(_this.schoolList, {
                        className: 'custom-classname',
                        container: 'body',
