            /**
             * 获取短信验证码
             * **/
            async yzm(phone) {
                if (!phone) {
                    weui.alert('请输入手机号')
                    return false
                }
                let params = {
                    str: phone
                }
                let submitInfo = await request('/api/open/sms/sendSms',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(submitInfo)
                if (submitInfo.code == 500) {
                    weui.alert(submitInfo.msg);
                    return false;
                }
                // loading.hide()
                if (!info) return;
                localStorage['yzm'] = submitInfo.msg
                weui.toast('已发送');
                let time = 60;
                let timer = setInterval(() => {
                    if (time == 0) {
                        clearInterval(timer);
                        this.isVerify = true;
                        this.yzmContent = "获取验证码";
                        return false;
                    } else if (time > 0) {
                        this.yzmContent = time + '秒后重试';
                        this.isVerify = false;
                        time--
                    }
                }, 1000)
                console.log(info)
            },
