            /**
             * 点击支付
             * **/
            async pay() {
                let that = this;
                if (that.studentName == '') {
                    alert("请输入学生姓名！")
                    // weui.toast("请输入学生姓名！")

                    return false;
                }
                if (that.studentPhone == '') {
                    alert("请输入手机号！")
                    return false;
                }
                if (that.verifyCode == '') {
                    alert("请输入短信验证码！")
                    return false;
                }
                if (that.schoolName == '') {
                    alert("请填写学校！")
                    return false;
                }
                if (that.studentArtsScience == '') {
                    alert("请填写学生文理！")
                    return false;
                }

                if (that.parentName == '') {
                    alert("请输入家长姓名！")
                    return false;
                }
                if (that.parentPhone == '') {
                    alert("请输入家长手机！")
                    return false;
                }
                if (that.sid == 0) {
                    alert('请先提交报名');
                    return false;
                }
                if (this.isTip) {
                    alert('订单已生成请勿重复提交');
                    return false;
                }
                let loading = weui.loading('loading', {
                    className: 'custom-classname'
                });
                /*先下订单*/
                let params = {
                    orderProductId: that.productId,
                    orderProductName: that.productName,
                    //actualPayment:String(that.price),
                    orderProductUnit: that.productUnit,
                    orderProductPrice: Number(this.price),
                    studentPhone: that.studentPhone,
                    schoolName: that.schoolName,
                    schoolId: that.schoolId,
                    orderProductNumber: 1,
                }
                if (that.$route.query.isOfficialAccount) {
                    /*公众号下单*/
                    params.orderType = 2;
                    params.orderActivityId = that.$route.query.activityId;
                } else {
                    if (this.$route.query.agentType == 1 || this.$route.query.agentType == 2
                    ) {
                        /*省代和市代下单*/
                        params.orderType = 2
                        params.orderAgentType = that.$route.query.agentType;
                        params.orderAgentInviteCode = that.inviteCode
                        params.agentId = that.$route.query.agentId ? that.$route.query.agentId : '';
                        params.agentName = that.$route.query.agentName ? that.$route.query.agentName : '';
                        params.orderAgentInviteCode = that.inviteCode;
                        if (this.$route.query.activityId) {
                            params.orderActivityId = that.$route.query.activityId;
                        }
                    } else if (this.$route.query.agentType == 3) {
                        /*二级分享代理下单*/
                        params.orderType = 1
                        params.orderAgentType = that.$route.query.agentType
                        params.agentId = that.$route.query.agentPid
                        params.agentName = that.$route.query.agentPname
                        params.orderAgentInviteCode = that.inviteCode
                        params.orderShareId = that.$route.query.agentId
                        params.orderShareName = that.$route.query.agentName

                    } else if (this.$route.query.agentType == 4) {
                        /*普通代理下单*/
                        params.orderType = 1
                        params.orderAgentType = that.$route.query.agentType
                        params.orderAgentInviteCode = that.inviteCode
                        params.orderShareId = that.$route.query.agentId
                        params.orderShareName = that.$route.query.agentName

                    }

                }
                let addOrderInfo = await request('/api/open/activity/insertActivityOrder',
                    'post', params, 'application/x-www-form-urlencoded')
                let info1 = result(addOrderInfo)
                if (!info1) return;
                let orderId = addOrderInfo.data
                this.isOrder = true
                /*下单成功，调用支付接口（统一下单），*/
                let params1 = {
                    attach: '',
                    body: that.productName,
                    orderId: orderId,
                    total_fee: Number(this.price * 100).toFixed(),
                    code: getParam('code')
                }
                let payInfo = await request('/api/open/wxPay/webPay',
                    'post', params1, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(payInfo);
                console.log(payInfo)
                if (payInfo.code == 500) {
                    this.isTip = true;
                    this.isOrder = false;
                    loading.hide()
                }
                if (!res) return
                /*成功拉起微信支付*/
                this.isTip = false;
                let info = JSON.parse(payInfo.msg)
                console.log(info);
                loading.hide()
                this.$nextTick(() => {
                    this.pullUpPay(info.appId, info.timeStamp, info.nonceStr, info.signType, info.paySign, info.package, orderId)
                })
            },
            /**
             *  拉起支付
             * */
            async pullUpPay(appId, timeStamp, nonceStr, signType, paySign, payId, orderId) {
                let that = this
                console.log(appId, timeStamp, nonceStr, signType, paySign, payId)

                function onBridgeReady() {
                    WeixinJSBridge.invoke(
                        'getBrandWCPayRequest', {
                            "appId": appId,   //公众号名称，由商户传入
                            "timeStamp": timeStamp,     //时间戳，自1970年以来的秒数
                            "nonceStr": nonceStr, //随机串
                            "package": payId,  // 统一下单接口返回的prepay_id 参数值
                            "signType": signType,     //微信签名方式：
                            "paySign": paySign //微信签名
                        },
                        async function (res) {
                            if (res.err_msg == "get_brand_wcpay_request:ok") {
                                /*支付成功，添加为该订单服务*/
                                this.isOrder = false;
                                let params2 = {
                                    access_token: that.token,
                                    orderId: orderId
                                }
                                let orderInfo = await request('/api/token/order/selectOrderInfoById',
                                    'post', params2, 'application/x-www-form-urlencoded')
                                // loading.hide()
                                let res = result(orderInfo)
                                if (!res) return;
                                console.log('订单信息', orderInfo)
                                let params = {
                                    access_token: that.token,
                                    orderCode: orderInfo.data.orderCode,
                                    orderId: orderId,
                                    agentId: orderInfo.data.orderAgentId ? orderInfo.data.orderAgentId : '',
                                    agentName: orderInfo.data.orderAgentName ? orderInfo.data.orderAgentName : '',
                                    schoolName: that.schoolName,
                                    expertId: orderInfo.data.orderExpertId ? orderInfo.data.orderExpertId : '',
                                    expertName: orderInfo.data.orderExpertName ? orderInfo.data.orderExpertName : '',
                                    studentName: that.studentName
                                }
                                if (that.$route.query.activityId) {
                                    params.activityId = that.$route.query.activityId
                                }
                                let addServeInfo = await request('/api/token/service/add',
                                    'post', params, 'application/x-www-form-urlencoded')
                                let info1 = result(addServeInfo)
                                console.log(addServeInfo)
                                if (!info1) return;
                                that.isMask = true;
                                that.movePanel = "down";
                                weui.toast('支付成功');
                                that.$nextTick(() => {
                                    history.go(-1);
                                })
                            } else {
                                this.isOrder = false;
                                weui.toast('支付失败');
                                let studentInfo = {
                                    studentName: that.studentName,
                                    studentPhone: that.studentPhone,
                                    schoolId: that.schoolId ? that.schoolId : '',
                                    schoolName: that.schoolName,
                                    studentTotalScore: that.studentTotalScore,
                                    studentRank: that.studentRank,
                                    studentArtsScienceId: that.studentArtsScienceId,
                                    studentArtsScience: that.studentArtsScience,
                                    parentName: that.parentName,
                                    parentPhone: that.parentPhone,
                                }
                                localStorage['studentInfo'] = JSON.stringify(studentInfo)
                                localStorage['payStatus'] = true;
                                that.$nextTick(() => {
                                    history.go(-1);
                                })
                            }
                        }
                    )
                }

                // 调用微信接口
                if (typeof WeixinJSBridge == "undefined") {
                    if (document.addEventListener) {
                        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                    } else if (document.attachEvent) {
                        document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                        document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                    }
                } else {
                    onBridgeReady();
                }
                ;
            }
            ,
            /**
             * 查看服务流程信息
             * **/
            goServiceProcess() {
                this.$router.push('/serviceProcess')
            }
            ,
        },


        /*watch:{
            showHeight() {
                if(this.docmHeight > this.showHeight){
                    this.topShow=true
                }else{
                    this.topShow=false
                }
            }
        }*/
    }
</script>

